@import 'Editor/_wickbrand.scss';

$asset-height: 24px;

.asset-item {
  width: 100%;
  height: $asset-height;
  line-height: $asset-height;
  background-color: $interface-tertiary;
  color: $interface-tertiary-text;
  margin-top: 4px;
  padding-left: 4px;
  vertical-align: center;
  border-radius: $interface-roundness-primary;
  overflow: hidden;

  display: flex;
  align-items: center;
}

.asset-item:hover {
  background-color: $interface-quaternary;
  color: $interface-tertiary-text;
  cursor: grab;
}

.asset-name-text {
  margin-left: 4px;
  white-space: nowrap;
  overflow: scroll;
}

.asset-item.asset-selected {
  transition: .2s; 
  background-color: $wick-green-dark;
  color: white;
}
